<template>
  <div class="container">
    <div v-if="responseData.errorcode === 503">
      <error code="503"></error>
    </div>
    <div v-if="responseData.status === true">
      <noteGet :noteContent="responseData.note"></noteGet>
    </div>
  	<div v-if="responseData.status === false">
      <div v-if="responseData.password === true">
        <div v-if="responseData.passwordmismatch === true">
          <error code="400"></error>
        </div>
        <p>{{ $t('views.noteid.note_is_password_protected_text') }}</p>
        <label>{{ $t('views.noteid.password_label') }}</label>
        <input type="text" v-model="notePassword" v-on:keyup.enter="unlockNote">
        <button v-on:click="unlockNote">{{ $t('views.noteid.unlock_note') }}</button>
      </div>
      <div v-else>
        <error code="404"></error>
      </div>
    </div>
  </div>
</template>

<script>
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import noteGet from './../components/noteGet'
import error from './../components/error'
export default {
  name: 'note',
  data () {
    return {
      responseData: [],
      notePassword: []
    }
  },
  mounted: function () {
    this.getNote()
  },
  methods: {
    getNote: function () {
      NProgress.start()
      var url = 'http://localhost:5000/note/' + this.$route.params.id
      this.$http.get(url).then(response => {
        let data = JSON.parse(response.body)
        console.log(data)
        this.responseData = data
        NProgress.done()
      }, response => {
        console.log(response)
        this.responseData = {'errorcode': 503}
        NProgress.done()
      })
    },
    unlockNote: function () {
      console.log('unlock note')
      this.responseData.passwordmismatch = false
      NProgress.start()
      var url = 'http://localhost:5000/note/' + this.$route.params.id + '?password=' + this.notePassword
      this.$http.get(url).then(response => {
        let data = JSON.parse(response.body)
        console.log(data)
        this.responseData = data
        NProgress.done()
      }, response => {
        console.log(response)
        this.responseData = {'errorcode': 503}
        NProgress.done()
      })
    }
  },
  components: {
    'noteGet': noteGet,
    'error': error
  }
}
</script>

<style scoped>
button {
  width: 100%;
  background-color: #9b4dca;
  border: 0.1rem solid #9b4dca;
  border-radius: .4rem;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-weight: 700;
  height: 2.75rem;
  letter-spacing: .1rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
}
input {
  font-family: monospace;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  border: 0.1rem solid #d1d1d1;
  border-radius: .4rem;
  box-shadow: none;
  box-sizing: border-box;
  height: 2.5rem;
  padding: .6rem 1.0rem;
  width: 100%;
  margin-bottom: 20px;
}
input:focus {
  border-color: #9b4dca;
  outline: 0;
}
.container {
  padding-top: 50px;
  padding-left: 12.5%;
  width: 75%;
  margin: 1px;
  padding-bottom: 25px;
}
</style>


