<template>
  <footer>
  <div class="container">
  	<p class="item">
  	<strong>&copy; 2017</strong><br>
      {{ $t('components.footer.maikeme_ltd') }}
  	</p>
  	<p class="item">
    <strong>{{ $t('components.footer.contact') }}</strong><br>
  	<a href="mailto:hello@note.delivery" :title="$t('components.footer.contact_us')">{{ $t('components.footer.email') }}</a>
  	</p>
  </div>
  <div class="container language">
    <button v-on:click="setLocale('en')">English</button> | 
    <button v-on:click="setLocale('dk')">dansk</button> |
    <button v-on:click="setLocale('cn')">中文</button> |
    <button v-on:click="setLocale('th')">ไทย</button>
  </div>
  </footer>
</template>

<script>
export default {
  name: 'navFooter',
  data () {
    return {
    }
  },
  methods: {
    setLocale (locale) {
      if (locale === this.$i18n.locale) {
        return
      }
      this.$i18n.locale = locale
      localStorage.setItem('locale', locale)
    }
  }
}
</script>

<style scoped>
footer{
	-webkit-box-flex: 0;
    -ms-flex: none;
    flex: none;
  font-size: .875rem;
  color:#818a91;
  border-top:1px solid #eceeef;
}
strong{
  color:#55595c;
}
img {
  width: 32px;
}
a {
  color: #8f979d;
  text-decoration: none;
  background-color: transparent;
}
.container {
  padding: 2rem 0;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
}

.item {
  display:inline-block;
    margin:0 4rem 0 0;
}

.float-right {
  margin-bottom:.5rem;
  margin:0;
  float:right;
}
.float-right:last-child {
   margin-bottom:0;
}
.language {
  text-align: center;
}
.language button {
  border: none;
  background: none;
}
</style>


