<template>
<div>
	<p>{{noteContent}}</p>
  <div v-if="responseData.status === true">
    <p>{{ $t('components.noteGet.successfully_deleted_note') }}</p>
  </div>
  <div v-if="responseData.status === false">
    <p>{{ $t('components.noteGet.failed_to_delete_note') }}</p>
  </div>
  <button v-on:click="deleteNote">{{ $t('components.noteGet.delete_note_button') }}</button>
</div>
</template>

<script>
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
export default {
  name: 'noteGet',
  props: ['noteContent'],
  data () {
    return {
      responseData: []
    }
  },
  methods: {
    deleteNote: function () {
      NProgress.start()
      var url = 'http://localhost:5000/note/' + this.$route.params.id + '/delete'
      this.$http.post(url).then(response => {
        let data = JSON.parse(response.body)
        this.responseData = data
        NProgress.done()
      }, response => {
        NProgress.done()
      })
    }
  }
}
</script>

<style scoped>
.container p {
  white-space: pre-wrap;
}
button {
  width: 100%;
  background-color: #9b4dca;
  border: 0.1rem solid #9b4dca;
  border-radius: .4rem;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-weight: 700;
  height: 2.75rem;
  letter-spacing: .1rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
}
</style>


