<template>
<div class="container">
  <label>{{ $t('components.noteCreate.note_label') }}</label>
  <textarea v-autosize v-model="noteText"></textarea>
  <label>{{ $t('components.noteCreate.password_label') }}</label>
  <input type="text" v-model="notePassword" v-on:keyup.enter="upload">
	<button v-on:click="upload">{{ $t('components.noteCreate.create_new_button') }}</button>
	<div v-bind="responseData" class="response-urls">
    <li v-for="data in responseData">
      <a v-bind:href="data.url" target="_blank">{{ data.shorturl }}</a>
    </li>
  </div>
  <div v-if="errors.code === 503">
    <error code="503"></error>
  </div>
  <div v-if="errors.code === 422">
    <error code="422"></error>
  </div>
</div>
</template>

<script>
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import error from './error'
export default {
  name: 'note',
  data () {
    return {
      responseData: [],
      noteText: [],
      notePassword: [],
      errors: []
    }
  },
  methods: {
    upload: function () {
      this.errors.code = false
      if (this.noteText.length === 0) {
        this.errors = {'code': 422}
        return
      }
      NProgress.start()
      var formData = new FormData()
      formData.append('note-textarea', this.noteText)
      formData.append('note-password', this.notePassword)
      this.$http.post('http://localhost:5000/note/new', formData).then(response => {
        let data = JSON.parse(response.body)
        console.log(data.url)
        // this.responseData = data
        this.responseData.unshift(data)
        NProgress.done()
      }, response => {
        console.log('error')
        this.errors = {'code': 503}
        NProgress.done()
      })
    }
  },
  components: {
    'error': error
  }
}
</script>

<style scoped>
label {
  color: #606c76;
  display: block;
  font-size: 1.1rem;
  font-weight: 700;
}
textarea {
  font-family: monospace;
	width: 100%;
	resize: none;
  border: 0.1rem solid #d1d1d1;
	box-sizing: border-box;
  border-radius: .4rem;
  padding: .6rem 1.0rem;
  margin-bottom: 1rem;
}
textarea:focus {
  border-color: #9b4dca;
  outline: 0;
}
button {
	width: 100%;
  background-color: #9b4dca;
  border: 0.1rem solid #9b4dca;
  border-radius: .4rem;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-weight: 700;
  height: 2.75rem;
  letter-spacing: .1rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
}
input {
  font-family: monospace;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  border: 0.1rem solid #d1d1d1;
  border-radius: .4rem;
  box-shadow: none;
  box-sizing: border-box;
  height: 2.5rem;
  padding: .6rem 1.0rem;
  width: 100%;
  margin-bottom: 20px;
}
input:focus {
  border-color: #9b4dca;
  outline: 0;
}
.container {
	padding-top: 50px;
	padding-left: 12.5%;
	width: 75%;
	margin: 1px;
  padding-bottom: 1.25rem;
}
.response-urls {
  margin-top: 1.25rem;
}
.response-urls li {
  list-style: none;
  margin-bottom: 0.25rem;
}
.response-urls li a {
  color: #3DC634;
  text-decoration: none;
  font-weight: bold;
  font-family: monospace;
  font-size: larger;
}
</style>


