import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

let locale = localStorage.getItem('locale') || 'en'

const i18n = new VueI18n({
  locale: locale,
  fallbackLocale: 'en',
  messages: {
    en: require('./en.json'),
    dk: require('./dk.json'),
    th: require('./th.json'),
    cn: require('./cn.json')
  }
})

export default i18n



