<template>
<div>
  <div v-if="code === '400'">
    <p>{{ $t('components.error.400_bad_request.wrong_password_text') }}</p>
  </div>
  <div v-if="code === '404'">
    <p>{{ $t('components.error.404_not_found.note_does_not_exist_text') }}</p>
  </div>
  <div v-if="code === '422'">
    <p>{{ $t('components.error.422_unprocessable_entity.cannot_create_empty_note_text') }}</p>
  </div>
  <div v-if="code === '503'">
    <p>{{ $t('components.error.503_service_unavailable.server_inaccessible_text') }}</p>
  </div>
</div>
</template>

<script>
export default {
  name: 'error',
  props: ['code'],
  data () {
    return {
    }
  }
}
</script>

<style scoped>
p {
  color: #E7593B;
  font-weight: bold;
  margin-bottom: 0rem;
}
</style>


